/* ###### 4.11 Spectrum ###### */

.sp-container {
	background-color: $white;
	border-color: #e8e7ef;
	z-index: 200;
  
	button {
	  border: 0;
	  padding: 8px 15px;
	  background-image: none;
	  background-color: #e2e8f5;
	  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, "Noto Sans", sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji";
	  font-size: 12px;
	  text-shadow: none;
	  text-transform: capitalize;
	  border-radius: 2px;
  
	  &:hover, &:focus {
		border: 0;
		background-image: none;
		background-color: #97a3b9;
		text-shadow: none;
	  }
	}
  }
  
  .sp-button-container .sp-cancel {
	font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, "Noto Sans", sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji";
	font-size: 12px;
	text-transform: capitalize;
	color: #8587a7 !important;
  
	&:hover, &:focus {
	  color: $dark !important;
	  text-decoration: none;
	}
  }
  
  .sp-picker-container {
	border-left: 0;
  }
  
  .sp-replacer {
	border-color: #e8e7ef;
	background-color: $white;
  
	&:hover, &:focus {
	  border-color: #b4bdce;
	}
  
	&.sp-active {
	  border-color: #8587a7;
	}
  }
  
  .sp-dd {
	text-indent: -99999px;
	position: relative;
	width: 10px;
  
	&::before {
	  content: '\f280';
	  font-family: 'Ionicons';
	  font-size: 15px;
	  color: #97a3b9;
	  position: absolute;
	  text-indent: 0;
	  left: 0;
	  z-index: 6;
	}
  }
  
  .sp-preview {
	border: 0;
  }
  
  .sp-dragger {
	background-color: transparent;
  }
  
  .sp-choose {
	background-color: #e2e8f5;
  }
  
  .sp-palette .sp-thumb-el {
	&:hover, &.sp-thumb-active {
	  border-color: $dark;
	}
  }